//Тут храняться миксины

@mixin test() {
    background-color: red;
}


@mixin page-header() {
    font-family: 'Futura New';
    font-style: normal;
    font-weight: 400;
    font-size: 54px;
    line-height: 130%;
    display: block;
    color: #000000;  
    
    @media screen and (max-width: 660px) {
        font-weight: 400;
        font-size: 34px;
        line-height: 130%;
    }
}

@mixin page-text() {
    font-family: 'Futura New';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 126%;
    color: #000000;    

}
@mixin page-btn() {
    display: inline-block;
    background: #FFFFFF;
    border-radius: 2px;
    padding: 12px 40px;
    box-sizing: border-box;

    font-weight: 450;
    font-size: 16px;
    line-height: 20px;
    
    color: #000000;   
    
    @media screen and (max-width: 960px) {
        padding: 10px 22px;
        font-size: 12px;
    }
}