    box-sizing: border-box;
    margin: 0 auto;
}

.wrapper {
    display: flex;
    flex-direction: column;
    height: 100%;
    min-height: calc(100*var(--vh, 1vh));
}

.header {
    flex-shrink: 0;
}

.main {
    flex-grow: 1;
    flex-shrink: 0;
}

.footer {
    flex-shrink: 0;
}

.section {}


