.breadcrumbs {}


.header {
    background: #fff;
    padding: 21px 0 29px;
    box-sizing: border-box;

    .container {}

    .header__wrapper {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .header__wrap {
        display: flex;
        justify-content: start;
        align-items: center;
        gap: 40px;
    }

    .header__logo {
        max-width: 70px;
        height: 29px;

        img {
            display: block;
            width: 100%;
        }
    }

    .header__nav {
        ._mob {
            display: none;
        }
    }

    .header__nav-list {
        display: flex;
        justify-content: start;
        align-items: center;
        gap: 16px;
    }

    .header__nav-item {
        a {
            font-weight: 400;
            font-size: 16px;
            line-height: 19px;

            color: #000000;
        }
    }

    .header__link {
        font-family: 'Manrope';
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 22px;
        /* identical to box height */

        flex-shrink: 0;
        color: #000000;
    }

    .header__search {
        position: relative;
        max-width: 194px;
        width: 100%;
    }

    .header__search-input {
        border: 1px solid #D8D8D8;
        background: #fff;
        padding: 9px 10px 9px 35px;
        box-sizing: border-box;

        width: 100%;
        display: block;
    }

    .header__search-submit {
        position: absolute;
        top: 50%;
        left: 10px;
        transform: translateY(-50%);
        padding: 0;
        border: none;
        background: transparent;
        height: 20px;

        svg {
            width: 20px;
            height: 20px;
        }
    }

    .header__controlls {
        display: flex;
        justify-content: start;
        align-items: center;
        gap: 20px;
    }

    .header__controll {
        svg {
            width: 20px;
            height: 20px;
        }
    }

    .header__burger-wrap {
        display: none;
    }

    .header__burger {
        width: 18px;
        height: 12px;
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;

        &::before,
        &::after {
            transition: .3s;
            content: '';
            width: 100%;
            height: 2px;
            background: #000;
            display: block;

            position: absolute;
            left: 0;
            top: 0;
        }

        &::after {
            top: unset;
            bottom: 0;
        }

        &._active {
            height: 18px;
            &::before,
            &::after {
                top: 50%;
                left: 50%;
                transform: translate(-50%) rotate(45deg);
            }
            &::after {
                transform: translate(-50%) rotate(-45deg);
            }
            .header__burger-item {
                display: none;
            }
        }
    }
    .header__burger-item {

        height: 2px;
        width: 100%;
        background: #000;
    }
}

.title {
    .title__slider-wrapper {
        position: relative;
    }

    .title__slider {
        box-sizing: border-box;
        margin-bottom: 0;
    }

    .title__slide {
        position: relative;
        min-height: 600px;
        display: flex;
        padding-bottom: 133px;
        box-sizing: border-box;
    }

    .title__slide-bg {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;

        &.md,
        &.sd {
            display: none;
        }
    }

    .title__slide__wrapper {
        position: relative;
        z-index: 10;
        width: 100%;
        margin-top: auto;
    }

    .container {}

    .title__slide__header {
        max-width: 580px;
        font-weight: 400;
        font-size: 72px;
        line-height: 126%;

        /* or 91px */
        text-transform: uppercase;

        color: #FFFFFF;

        b {
            font-weight: 700;
        }
    }

    .title__dots-wrapper {
        position: absolute;
        bottom: 26px;
        left: 50%;
        transform: translateX(-50%);
        z-index: 10;

        .slick-dots {
            position: static;

            li {
                width: 8px;
                height: 8px;
                border: 1px solid #fff;
                border-radius: 10px;

                &.slick-active {
                    button {
                        opacity: 1;
                    }
                }

                button {
                    transition: opacity .3s;
                    opacity: 0;
                    padding: 0;
                    width: 8px;
                    height: 8px;
                    background: #fff;

                    &::before {
                        display: none;
                    }
                }
            }
        }
    }
}

