    .card__left__slider-wrapper {}

    .card__left__slider {}

    .card__left__slide {
        img {
            display: block;
            width: 100%;
        }
    }

    .card__right {
        padding-left: 28px;
        box-sizing: border-box;

        display: flex;
        justify-content: space-between;
        flex-direction: column;
    }

    .card__right-text {
        margin-bottom: 60px;
    }

