        input {
            position: absolute;
            top: 0;
            left: 0;
            opacity: 0;

            &:checked+span::after {
                background: #81b8ec;
            }
        }

        span {
            &::after {
                content: '';
                width: 100%;
                height: 1px;
                background: #f1f1f1;
                display: block;

                position: absolute;
                bottom: 0;
                left: 0;
            }
