        background: transparent;
        outline: none;
        margin: 0;
        padding: 0;
        cursor: pointer;

        display: flex;
        justify-content: center;
        align-items: center;

        svg {
            width: 36px;
            height: 16px;
        }

        &:first-child {
            transform: scale(-1);
        }

        &.slick-disabled {
            opacity: .2;
        }
    }

    .card__right__slider-header {
        font-weight: 400;
        font-size: 32px;
        line-height: 138%;
        /* or 44px */
        letter-spacing: -0.02em;

        color: #000000;
    }

    .card__right__slider {}

    .card__right__slide {
        padding: 0 28px 0 0;

        a {}

        img {
            display: block;
            max-height: 168px;
            width: 100%;
            object-fit: cover;
        }
    }

    .card__right-bottom {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .card__right-price {
        font-weight: 400;
        font-size: 32px;
        line-height: 38px;

        color: #000000;
    }

    .card__right-add {
        background: #000;
        color: #fff;
        border: none;
    }

    .card__footer {
        padding-top: 50px;
        box-sizing: border-box;
        border-top: 1px solid #D8D8D8;
    }
    .card__footer-mobile {
        display: none;
    }

    .card__footer-tabs {}

    .card__footer-tabs__list {
        display: flex;
        justify-content: start;
        align-items: center;
        border-bottom: 1px solid #F1F1F1;
        width: max-content;
