        box-sizing: border-box;

        border-bottom: 1px solid #F1F1F1;

        &._active {
            border-color: #81B8EC;
        }
    }

    .card__footer-content__wrapper {}

    .card__footer-content {

        &:not(._active) {
            display: none;
        }

        p {}
    }
}

