        grid-template-columns: 266px auto;
        gap: 28px;
        margin-top: 40px;
        padding-top: 40px;
        box-sizing: border-box;
        border-top: 1px solid #D8D8D8;
    }

    .lk-body__nav {}

    .lk-body__nav-list {
        border-bottom: 1px solid #D8D8D8;
    }

    .lk-body__nav-item {

        &._active {
            a {
                background: #F1F1F1;
            }
        }

        a {
            font-weight: 400;
            font-size: 20px;
            line-height: 18px;
            color: #000000;

            display: block;
            padding: 20px 10px;
            box-sizing: border-box;
        }
    }

    .lk-body__user-data {}

    .lk-body__user-data__form {
        background: #FBFBFB;
        padding: 40px;
        box-sizing: border-box;

        display: flex;
        flex-direction: column;
        justify-content: start;
        align-items: flex-start;
    }

    .lk-body__label {
        width: 100%;
        position: relative;
        padding-top: 26px;
        box-sizing: border-box;

        span {
            position: absolute;
            top: 0;
            left: 0;

            font-weight: 400;
            font-size: 16px;
            line-height: 18px;
            color: #000000;
        }

        &:not(:last-child) {
            margin-bottom: 20px;
        }

        &._triple-field {
            display: grid;
            grid-template-columns: 128px 128px auto;
            gap: 28px;

            input[type="text"] {
                text-align: center;
            }
        }
    }

    .lk-body__input {
        border: 1px solid #7C7C7C;
        padding: 19px 10px;
        box-sizing: border-box;
        width: 100%;

        font-weight: 400;
        font-size: 16px;
        line-height: 18px;
    }

    .lk-body__user-data__submit {
