.search {
    padding: 32px 0;
    box-sizing: border-box;

    .container {

    }
    .search__wrapper {
        display: flex;
        flex-direction: column;
        height: 100%;
    }
    .search__header {
        font-weight: 400;
        font-size: 16px;
        line-height: 19px;
        /* identical to box height */
        color: #000000;
        .num {
            font-weight: 600;
        }
    }
    .search__list {
        margin-top: 32px;
        padding-top: 32px;
        border-top: 1px solid #D8D8D8;
        box-sizing: border-box;
    }
    .search__item {

        &:not(:last-child) {
            margin-bottom: 16px;
        }
    }
    .search__item-link{
        border: 1px solid #D8D8D8;
        padding: 24px;
        box-sizing: border-box;
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
    }
    .search__item__wrapper {
        display: flex;
        justify-content: start;
        align-items: flex-start;
        gap: 28px;
    }
    .search__img {
        width: 100px;
        height: 100px;
        flex-shrink: 0;
    }
    .search__name {
        max-width: 229px;
    }
    .search__price {
        font-weight: 450;
        font-size: 24px;
        line-height: 30px;
        /* identical to box height */
        text-align: right;
        color: #000000;        
    }
}


.footer {
    background: #FAFAFA;
    padding: 24px 0;
    box-sizing: border-box;

    .container {}

    .footer__wrapper {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .footer__top {
        .footer__wrapper {
            border-bottom: 1px solid #DCDCDC;
            padding-bottom: 30px;
        }

        margin-bottom: 16px;
    }

    .logo {}

    .footer__nav {}

    .footer__nav__list {
        display: flex;
        justify-content: start;
        align-items: center;
        gap: 24px;
    }

    .footer__nav__item {
        a {
            font-weight: 400;
            font-size: 14px;
            line-height: 22px;
            color: #000000;
        }
    }

    .footer__bot {
        .footer__nav__item a {
            font-weight: 600;
            font-size: 14px;
            line-height: 22px;
        }
    }

    .copy {
        font-weight: 400;
        font-size: 14px;
        line-height: 22px;
        color: #000000;
    }

    .footer__epilog {
        margin-top: 30px;
        display: block;
        text-align: center;

        font-weight: 400;
        font-size: 14px;
        line-height: 22px;

        /* identical to box height, or 157% */

        color: #000000;

        a {
            font-weight: 600;
            color: #000000;
        }
    }
}


